body{
  font-size: 18px;
  font-family: sans-serif;
  box-sizing: border-box;
  background: #f1f1f1;
}
.App{
  max-width: 750px;
  width: 90%;
  margin: 0 auto 50px auto;
}
a:link, a:visited{
  text-decoration: none;
  color: black;
  padding-bottom: 1px;
  border-bottom: 1px solid transparent;
  transition: border-bottom 0.25s;
}
a:hover, a:active{
  border-bottom: 1px solid black;
}
nav ul{
  list-style: none;
}
nav{
  display: grid;
  grid-template-columns: auto 1fr auto;
}
nav h3{
  grid-column: 1/2;
}
nav h3 a{
  font-size: 150%;
}
nav ul{
  grid-column: 3/4;
  padding-top: 10px;
}
nav ul li{
  padding: 25px;
  display: inline;
}
.articles>h1{
  text-align: center;
  margin-bottom: 50px;
}
.articles a:hover{
  color: rgb(82, 76, 76);
}
.articles>h1:after {
  display: block;
  height: 2px;
  background-color: black;
  content: " ";
  width: 100px;
  margin: 0 auto;
  margin-top: 15px;
}
.form>h1{
  text-align: center;
}
.form>div>label{
  display: grid;
  grid-template-columns: 1fr 75%;
  margin-bottom: 15px;
}
input, textarea{
  width: 100%;
  padding: 7px;
  border-radius: 3px;
  border: 1px solid #ccc;
}
button {
  margin-left: 25%;
  border: 0;
  background-color: black;
  display: block;
  padding: 10px 30px;
  text-decoration: none;
  border-radius: 200px;
  color: white;
  transition: background-color 0.25s, color 0.25s;
}
button:hover{
  background-color: rgb(82, 76, 76);
  color: white;
}
.about>h1{
  text-align: center;
  margin-bottom: 50px;
}
.upvote{
  display: grid;
  grid-template-columns: 100px auto;
}
.upvote>button{
  padding: 5px 5px;
  margin-left: 0;

}
.upvote>p{
  margin: 10px 30px;
}
.comments{
  margin-left: 5%;
}
@media screen and (max-width: 600px) {
  body{
    font-size: 15px;
  }
}